











































import { Vue } from 'vue-property-decorator';
import { Component, Mixin, Mixins } from 'vue-mixin-decorator';
import { vxm } from '@/store';
import PortfolioSection from '@/components/PortfolioSection.vue';
import Profile from '@/components/Profile.vue';
import Project from '@/components/Project.vue';
import PortfolioFooter from '@/components/PortfolioFooter.vue';
import Projects from '@/components/Projects.vue';
import GithubData from '@/services/github-data';
import { GithubRepo } from '@/models/github-repo';
import { GithubUser } from '@/models/github-user';
import { ConfigMixin } from '@/mixins/config';

@Component({
  components: {
    PortfolioSection,
    Profile,
    Project,
    Projects,
    PortfolioFooter,
  },
})
export default class Home extends Mixins<ConfigMixin>(ConfigMixin) {
  private get User() {
    return vxm.user.gitUser;
  }

  private get Sections() {
    return vxm.repo.sections;
  }

  private mounted() {
    vxm.user.loadUser();
    vxm.repo.loadRepos();
  }
}
